import request from "@/utils/request";

export function list(params) {
  return request({
    url: "/tenant/list",
    method: "post",
    headers: {
      "Content-Type": "application/json"
    },
    data: params
  });
}

export function get(id) {
  return request({
    url: "/tenant/" + id,
    method: "get"
  });
}

